.landing {
    height:100vh;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    text-align:center;
}

.gear {
    max-width: 100%;
    margin-top: 0;
    max-height: 26rem;
}

.head {
    max-width: 100%;
    margin-top: 0;
    max-height: 26rem;
    left: .24rem;
}

.register {
    background-color: #13D2CC !important;
    border: none !important;
    font-family: 'Sen', sans-serif !important;
    transition: .3s;
}

.register:hover {
    background-color: #815AE0 !important;
}

@-webkit-keyframes rotation-gears {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}

@keyframes rotation-gears {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
                transform: rotate(359deg);
    }
}

.rotate-gears {
    -webkit-filter: drop-shadow(0 0 0.5rem #000000);
            filter: drop-shadow(0 0 0.5rem #000000);
    -webkit-animation: rotation-gears 15s infinite linear;
            animation: rotation-gears 15s infinite linear;
}

@media (max-width: 576px) {
    .gear {
        max-width: 100%;
        margin-top: 0;
        max-height: 20rem;
    }

    .head {
        max-width: 100%;
        margin-top: 0;
        max-height: 20rem;
        left: .24rem;
    }
}
.navbar {
