}

.navText {
    color: white !important;
    font-family: 'Sen', sans-serif !important;
    font-size: 1.5rem !important;
    transition: all .1s ease-in-out !important; 
}

.navText:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.content {
      max-width: 1280px;
      overflow-x: hidden;
      margin: 0 auto;
      font-family: "Avenir Next Condensed";
    }
    
    .card {
      height: 100%;
      display: flex;
      flex-direction: column;
      max-width: 24rem;
      position: relative;
      border: 1px solid #ccc;
    }
    
    .card-header {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    
    .card-header span {
      color: #ffffbd;
    }
    
    .card-image {
      width: 100%;
      height: 14rem;
      max-width: 100%;
      object-fit: cover;
    }
    
    .card-button {
      display: inline-block;
      background-color: #c471dc;
      color: #fff;
      border: none;
      padding: 1.5em 1.6em;
      cursor: pointer;
      text-decoration: none;
      font-size: 15px;
      font-family: inherit;
      width: 100%;
      position: absolute;
      bottom: 0;
      transition: background-color 0.5s ease-out;
    }
    
    .text-info {
      padding: 1rem 1rem;
     
    }
